'use client'

import React, { createContext, useContext, useState, useEffect, useCallback } from 'react'

// ── Types ───────────────────────────────────────────────
export type ThemeName = 'light' | 'dark' | 'high-contrast' | 'colorblind' | 'calm'

export interface AccessibilitySettings {
    theme: ThemeName
    fontSize: number        // 12–32
    fontFamily: 'inter' | 'opendyslexic'
    lineHeight: number      // 1.2–2.5
    letterSpacing: number   // 0–4
    reducedMotion: boolean
    cursorSize: 'normal' | 'large' | 'xlarge'
    dwellTime: number       // ms for eye gaze
    scanSpeed: number       // ms for switch scanning
    tremorFilter: number    // 0–100 strength
    morseThresholds: { dot: number; dash: number; letterGap: number; wordGap: number }
    ttsVoice: string
    ttsSpeed: number
    ttsPitch: number
    sosEnabled: boolean
    sosTrigger: 'blinks' | 'taps' | 'voice'
    sosContacts: string[]
}

export interface OutputState {
    text: string
    history: string[]
    historyIndex: number
}

interface FluentContextType {
    settings: AccessibilitySettings
    updateSetting: <K extends keyof AccessibilitySettings>(key: K, value: AccessibilitySettings[K]) => void
    resetSettings: () => void
    output: OutputState
    appendOutput: (text: string) => void
    setOutput: (text: string) => void
    clearOutput: () => void
    undoOutput: () => void
    redoOutput: () => void
    speak: (text?: string) => void
    stopSpeaking: () => void
    isSpeaking: boolean
    sidebarOpen: boolean
    setSidebarOpen: (open: boolean) => void
}

// ── Defaults ────────────────────────────────────────────
const DEFAULT_SETTINGS: AccessibilitySettings = {
    theme: 'light',
    fontSize: 16,
    fontFamily: 'inter',
    lineHeight: 1.6,
    letterSpacing: 0,
    reducedMotion: false,
    cursorSize: 'normal',
    dwellTime: 1200,
    scanSpeed: 1500,
    tremorFilter: 0,
    morseThresholds: { dot: 200, dash: 400, letterGap: 1000, wordGap: 2000 },
    ttsVoice: '',
    ttsSpeed: 1.0,
    ttsPitch: 1.0,
    sosEnabled: true,
    sosTrigger: 'taps',
    sosContacts: [],
}

const DEFAULT_OUTPUT: OutputState = { text: '', history: [''], historyIndex: 0 }

const FluentContext = createContext<FluentContextType | null>(null)

export function useFluentContext() {
    const ctx = useContext(FluentContext)
    if (!ctx) throw new Error('useFluentContext must be inside FluentProvider')
    return ctx
}

// ── Provider ────────────────────────────────────────────
export function FluentProvider({ children }: { children: React.ReactNode }) {
    const [settings, setSettings] = useState<AccessibilitySettings>(DEFAULT_SETTINGS)
    const [output, setOutputState] = useState<OutputState>(DEFAULT_OUTPUT)
    const [isSpeaking, setIsSpeaking] = useState(false)
    const [sidebarOpen, setSidebarOpen] = useState(true)

    // Load settings from localStorage
    useEffect(() => {
        try {
            const saved = localStorage.getItem('fluent-settings')
            if (saved) setSettings({ ...DEFAULT_SETTINGS, ...JSON.parse(saved) })
        } catch { }
    }, [])

    // Persist settings
    useEffect(() => {
        try { localStorage.setItem('fluent-settings', JSON.stringify(settings)) } catch { }
    }, [settings])

    // Apply theme class + CSS variables
    useEffect(() => {
        const root = document.documentElement
        root.setAttribute('data-theme', settings.theme)
        root.style.setProperty('--user-font-size', `${settings.fontSize}px`)
        root.style.setProperty('--user-line-height', `${settings.lineHeight}`)
        root.style.setProperty('--user-letter-spacing', `${settings.letterSpacing}px`)
        if (settings.reducedMotion) root.classList.add('reduce-motion')
        else root.classList.remove('reduce-motion')
        if (settings.fontFamily === 'opendyslexic') root.classList.add('font-dyslexic')
        else root.classList.remove('font-dyslexic')
    }, [settings])

    const updateSetting = useCallback(<K extends keyof AccessibilitySettings>(
        key: K, value: AccessibilitySettings[K]
    ) => {
        setSettings(prev => ({ ...prev, [key]: value }))
    }, [])

    const resetSettings = useCallback(() => setSettings(DEFAULT_SETTINGS), [])

    // ── Output management ─────────────────────────────────
    const appendOutput = useCallback((t: string) => {
        setOutputState(prev => {
            const newText = prev.text + t
            const newHistory = [...prev.history.slice(0, prev.historyIndex + 1), newText]
            return { text: newText, history: newHistory, historyIndex: newHistory.length - 1 }
        })
    }, [])

    const setOutputText = useCallback((t: string) => {
        setOutputState(prev => {
            const newHistory = [...prev.history.slice(0, prev.historyIndex + 1), t]
            return { text: t, history: newHistory, historyIndex: newHistory.length - 1 }
        })
    }, [])

    const clearOutput = useCallback(() => {
        setOutputState(prev => {
            const newHistory = [...prev.history.slice(0, prev.historyIndex + 1), '']
            return { text: '', history: newHistory, historyIndex: newHistory.length - 1 }
        })
    }, [])

    const undoOutput = useCallback(() => {
        setOutputState(prev => {
            if (prev.historyIndex <= 0) return prev
            const idx = prev.historyIndex - 1
            return { ...prev, text: prev.history[idx], historyIndex: idx }
        })
    }, [])

    const redoOutput = useCallback(() => {
        setOutputState(prev => {
            if (prev.historyIndex >= prev.history.length - 1) return prev
            const idx = prev.historyIndex + 1
            return { ...prev, text: prev.history[idx], historyIndex: idx }
        })
    }, [])

    // ── TTS ───────────────────────────────────────────────
    const speak = useCallback((text?: string) => {
        const t = text || output.text
        if (!t || typeof window === 'undefined') return
        window.speechSynthesis.cancel()
        const u = new SpeechSynthesisUtterance(t)
        u.rate = settings.ttsSpeed
        u.pitch = settings.ttsPitch
        if (settings.ttsVoice) {
            const voice = window.speechSynthesis.getVoices().find(v => v.name === settings.ttsVoice)
            if (voice) u.voice = voice
        }
        u.onend = () => setIsSpeaking(false)
        u.onerror = () => setIsSpeaking(false)
        setIsSpeaking(true)
        window.speechSynthesis.speak(u)
    }, [output.text, settings.ttsSpeed, settings.ttsPitch, settings.ttsVoice])

    const stopSpeaking = useCallback(() => {
        window.speechSynthesis.cancel()
        setIsSpeaking(false)
    }, [])

    return (
        <FluentContext.Provider value={{
            settings, updateSetting, resetSettings,
            output, appendOutput, setOutput: setOutputText, clearOutput, undoOutput, redoOutput,
            speak, stopSpeaking, isSpeaking,
            sidebarOpen, setSidebarOpen,
        }}>
            {children}
        </FluentContext.Provider>
    )
}
